import React from 'react'
import ReactDOM from 'react-dom'
import Header from './shoukong'

class Demo extends React.Component{
  render(){
    return (
      <div>
      {/* up和down是传布尔值，控制上下是否出现;title传入的是什么就显示什么,slot是插槽，位于up上方*/}
      <Header title="hei" up={true} down={false} slot={<p>我是插槽</p>}/>
      </div>
    )
  }
}

ReactDOM.render(
  <Demo />,
  document.getElementById('root')
);
